export default {
	data() {
		return {
			title: '',
			loadMoreText: "加载中...",
			showLoadMore: false,
			list: [],
			skip: -1,
			max: false //是否已经达到最大值 没有更多了
		}
	},
	onLoad() {
		this.getArticleList()
	},
	onUnload() {
		this.max = false,
		this.list = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.max) {
			return;
		}

		this.showLoadMore = true;
		setTimeout(() => {
			this.getArticleList()
		}, 300);
	},

	methods: {
		getArticleList() {
			this.skip++;

			let params = {
				skip: this.skip
			}
			if (this.title != "") {
				params.title = this.title
			}

			uni.request({
				url: this.apiBasic + '/articles',
				method: 'GET',
				data: params,
				header: {
					'Authorization': 'Bearer ' + uni.getStorageSync("SToken")
				},
				success: res => {

					let data = res.data;
					if (data.length == 0) {
						this.showLoadMore = true;
						this.loadMoreText = "没有更多数据了!"
						this.max = true;
						return;
					}

					data.createdAt = new Date(data.createdAt).toLocaleString();

					for (let item of data) {
						let content = JSON.parse(item.description)
						content = content.filter(item => item.type == "image");
						item.description = content;
					}

					this.list = this.list.concat(data);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		goEdit(_id) {
			uni.redirectTo({
				url: '/pages/write/write?id=' + _id
			});
		},
		formSubmit() {
			this.showLoadMore = false;
			this.list = [];
			this.skip = -1;
			this.getArticleList()
		}
	}
}
